:root {
  --trunk-color: #583928;
  --branch-color: #155132;
  --leaf-color: #2d6a3e;
  --flower-color: #93d17a;
  --logo-text-color: #35231b;
  --background-color: #fff9e2;
  --main-text-color: #593c1f;
  --alt-text-color: #556b2f;
  --head-text-color: #22551c;
  --title-text-color: #228b22;
}

html {
  -webkit-text-size-adjust: 100%;
}

body {
  color: var(--main-text-color);
  background-color: var(--background-color);
  flex-direction: column;
  min-height: 100vh;
  font-family: Arial, sans-serif;
  line-height: 1.6;
  display: flex;
}

h1 {
  color: var(--title-text-color);
  font-size: 2em;
}

h2 {
  color: var(--title-text-color);
  font-size: 1.5em;
}

p {
  margin-bottom: 10px;
  font-size: 1em;
}

ul {
  margin-left: 20px;
  list-style-type: disc;
}

li {
  margin-bottom: 5px;
}

li::marker {
  color: var(--title-text-color);
}

header {
  background-color: var(--background-color);
  color: var(--alt-text-color);
  text-align: center;
  justify-content: center;
  align-items: center;
  padding-bottom: 25px;
  display: flex;
}

header aside {
  display: inline-block;
}

header img {
  width: 256px;
}

header h1 {
  color: var(--logo-text-color);
  margin: 0;
}

header h2 {
  color: var(--title-text-color);
  margin: 0;
}

main {
  resize: both;
  background-image: url("watermark.aeec90e7.png");
  background-size: 1280px;
  flex: 1;
  max-width: 1280px;
  height: 100%;
  margin: auto;
}

footer {
  min-height: 50px;
}
